<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="110px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">待提交</span></h2>
                    <p class="fs-14">当前处理人：admin</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车牌号">
                                <el-button @click="chooseCar" :disabled="isDsiabledBase()">{{form.base_info.plate_number}}</el-button>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="车主姓名">
                                {{form.base_info.band_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="vin号">
                                {{form.base_info.vin_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="移动电话">
                                {{form.base_info.mobile}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车辆品牌">
                                {{form.base_info.car_brand}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="身份证号">
                                {{form.base_info.id_card}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公告型号">
                                {{form.base_info.foreign_model}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="挂靠公司">
                                {{form.base_info.bus_mech_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="马力">
                                {{form.base_info.horse_power}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="驱动形式">
                                {{form.base_info.drive_form}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="颜色">
                                {{form.base_info.color}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　备注信息" name="2">
                    
                    <el-form-item label="车辆现状资料">
                        <el-upload
                            :disabled="isDsiabledBase()"
                            :action="IMG_UPLOAD_URL"
                            :on-success="handleUploadSuccess_bus_status"
                            :file-list="form.bus_status"
                            list-type="picture-card">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-form-item>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.bus_remark" :disabled="isDsiabledBase()"></el-input>
                    </el-form-item>

                    <el-form-item label="选择审核人" class="mt15" v-if="isShowFirstAudit()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    
                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="3" v-if="isShowAudit()">
                    
                    <el-card class="box-card mb15" v-for="(item,index) in form.audit_info.pay" :key="index">
                        <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                        <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                        <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                        <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                        <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                    </el-card>

                    <template v-if="isShowOpinion()">
                        <el-form-item label="审核意见" class="mt15">
                            <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                <el-option label="通过" value="1"></el-option>
                                <el-option label="驳回" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="备注">
                            <el-input type="textarea" v-model="form.remark"></el-input>
                        </el-form-item>
                        <el-form-item label="选择审核人" class="mt15" v-if="form.opinion==1">
                            <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </template>

                    <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-if="pageType != 'view'">提 交</el-button>
                <!-- <el-button @click="dialogClose = true">关闭</el-button> -->
                <el-button type="primary" @click="handleSubmit(2)" v-if="isShowSave()">保 存</el-button>
                <el-button @click="$router.back()">返 回</el-button>
            </el-form-item>
        </el-form>

        <!-- 关闭 -->
        <el-dialog title="关闭" :visible.sync="dialogClose" width="50%">
            <el-form :inline="true" :model="filtersClose">
                <el-tag type="danger">确认关闭该工单吗？如确认关闭请填写备注信息。</el-tag>
                <el-form-item label="事故备注">
                    <el-input type="textarea" v-model="filtersClose.msg"></el-input>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogClose = false">取 消</el-button>
                <el-button type="primary" @click="">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 选择车辆 -->
        <el-dialog title="选择车辆" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar v-if="hackReset" :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>

    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import { organizationListUl } from '@/api/enterpriseManagement'
    import { requestUrl } from '@/api/config'
    import selectCar from '@/components/selectCar'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                pageType: '',
                loading: false,
                hackReset: false,
                optionsExecUser:[],
                optionsCompany:[],
                dialogSelectCarType: false,
                selectCarOptions:{
                    tableConfig:{
                        plate_number: true,
                        vin_number: true,
                        band_name: true,
                        car_info: true
                    },
                    formConfig: {
                        type: true,
                        brand: true,
                        series: true,
                        plate_number: true,
                    },
                    pageType: 'insuranceOrder'
                },
                dialogClose: false,
                filtersClose: {
                    msg: ''
                },
                form: {
                    cp_id:'',
                    opinion: '1',
                    after_user_id: "",
                    remark: "",
                    bus_remark: "",
                    node_id: "0",
                    some_collect_cast: {
                        money: "0",
                        type: "7",
                        item_id: "1",
                        pay_type: "1",
                        pay_method: 1,
                        this_cost: "0",
                        non_payment: "",
                        no_pay_id: "",
                        together_cost: "0",
                        cost_img: "",
                        remark: "",
                        status: "1",
                        create_time: "0",
                        update_time: "0"
                    },
                    base_info: {
                        vin_number: "",
                        car_brand: "",
                        car_series: "",
                        car_class: "",
                        foreign_model: "",
                        fuel_type: "",
                        horse_power: "",
                        drive_form: "",
                        color: "",
                        band_id: "",
                        band_name: "",
                        bus_mech_name: "",
                        plate_number: "选择",
                    }
                }
            }
        },
        components:{
            selectCar
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            isDsiabledBase(){
                return this.form.node_id!=270 && this.form.node_id!=273 && this.form.node_id!=0
            },
            isShowAudit(){
                return this.form.node_id!=270 && this.form.node_id!=0
            },
            isShowSave(){
                if(this.pageType!='view'){
                    if(this.form.node_id==270 || this.form.node_id==273 || this.form.node_id==0){
                        return this.form.work_status!=2
                    }
                }
            },
            isShowFirstAudit(){
                return this.form.node_id==270 || this.form.node_id==0
            },
            isShowOpinion(){
                return this.form.work_status==2
            },
            isShowLastAudit(){
                return this.form.work_status==1 || this.form.work_status==3
            },
            // 选择车辆
            chooseCar(){
                this.dialogSelectCarType = true
                this.hackReset = false
                this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;

                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                this.loading=true
                API.apiAnomalyCarScrapView({id: _query.id}).then(res => {
                    this.loading=false
                    if(res.code == 0 && res.data){
                        Object.assign(this.form, res.data)
                        this.getExecUser()
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getExecUser(){
                // 审核人
                let type='',
                    get_type='3'
                this.form.some_collect_cast.pay_method==1?type=1:type=2
                let params = {
                    cp_id:this.form.cp_id,
                    type:type,
                    get_type:get_type,
                    node_id:this.form.node_id,
                }
                this.form.after_user_id = ''
                API.apiAnomalyCarScrapAuditUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleSubmit(submitType) {
                
                if(this.form.node_id==270 || this.form.node_id==273 || this.form.node_id==0){
                    let params = {
                        id: this.$route.query.id,
                        operation_uniqid:this.form.operation_uniqid,
                        bus_remark: this.form.bus_remark,
                        bus_status: this.form.bus_status,
                        after_user_id: this.form.after_user_id,
                        submit_type: submitType,
                    }
                    API.apiAnomalyCarScrapAbshSave(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                    let params = {
                        id: this.form.id,
                        operation_uniqid:this.form.operation_uniqid,
                        submit_type: submitType,
                        exam_type: this.form.opinion,
                        after_user_id: this.form.after_user_id,
                        remark: this.form.remark,
                    }
                    API.apiAnomalyCarScrapAbshAudit(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
            },
            handleUploadSuccess_bus_status(res, file, fileList){
                this.form.bus_status.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleBeforeUpload(){

            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                this.dialogSelectCarType = false;
                Object.assign(this.form.base_info,row)
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>